v-if=\"isSampleLogoSelected\" :src=\"selectedLogo\" class=\"sample-logo-preview\"/>\n            </label>\n            <div v-if=\"!(!doesHaveLogo && !isLogoUploaded && !isSampleLogoSelected)\" class=\"remove-logo\" @click=\"removeLogo()\">\n                <i class=\"mdi mdi-delete\"></i>\n            </div>\n        </div>\n        <div>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/planet-ringed.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/puzzle.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/house-building.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/car.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/ice-cream.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/gears.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/sack-dollar.svg\"/>\n            <img class=\"sample-logo\" @click=\"chooseSampleLogo($event)\" src=\"../../../../images/sampleAppLogos/robot.svg\"/>\n        </div>\n        <StudioInfo style=\"margin:32px 0px 52px\" :infoText=\"'This app image will be displayed as an app logo on mobile and favicon on web browser.'\"></StudioInfo>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button :disabled=\"!isLogoUploaded && !isSampleLogoSelected\" id=\"sendButton\" class=\"save-button\" @click=\"save()\">SAVE</button>\n        </div>\n        \n    </div>\n</template>\n\n<script setup lang=\"ts\">\n    import { computed, ref } from \"vue\";\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n    import { INotification } from \"../../../../domain/presentation/INotification\";\n    import { IApplication } from \"../../../../domain/model/models\"\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IApplicationUseCase } from \"../../../../domain/useCase/IApplicationUseCase\";\n    import { IUpdateApplicationRequest } from \"../../../../../../common/qCloudTemp/symDtoObjects\";\n    import StudioInfo from \"../ui/studioInfoComp.vue\"\n\n    interface IAppLogo {\n        app?: IApplication;\n    }\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const applicationUseCase = container.resolve<IApplicationUseCase>(IApplicationUseCase);\n    const notification = container.resolve<INotification>(INotification);\n\n    const props = defineProps<IAppLogo>();\n    const emit = defineEmits([\"close\"]);\n    \n    const isLogoUploaded = ref(false);\n    const isSampleLogoSelected = ref(false);\n    const selectedLogo = ref(\"\");\n    const selectedLogoRequest = ref<IUpdateApplicationRequest[\"logo\"]>();\n    const doesHaveLogo = ref(props.app!.logoInfo ? true : false);\n\n    function close() {\n        emit(\"close\");\n    }\n    function getLogo (event: Event) {\n        let file = (document.getElementById(\"appLogo\")! as HTMLInputElement).files;\n        if(file![0].size > 1_000_000) { //1MB //OLD: 100000\n            event.preventDefault();\n            notification.showNotification({type: \"warning\", text: \"File size cannnot be bigger than 1MB\"});\n        }\n        else {\n            toBase64fromUpload(file![0]);\n            isLogoUploaded.value = true;\n            isSampleLogoSelected.value = false;\n            \n        }\n    }\n    function toBase64fromUpload(file: File) {\n        let reader = new FileReader();\n        let logo: string | undefined = undefined;\n        reader.readAsDataURL(file); //convert to base64\n        reader.onload = function() {\n            logo = reader.result!.toString();\n            selectedLogo.value = logo;\n            selectedLogoRequest.value = { dataBase64: logo.split(\",\")[1], contentType: file.type as any, fullName: file.name };\n        }\n    }\n    function chooseSampleLogo (event : Event) {\n        isLogoUploaded.value = false;\n        isSampleLogoSelected.value = true;\n        let sampleLogoSrc = (event.target as HTMLImageElement).src;\n        toBase64fromSample(sampleLogoSrc);\n    }\n    function toBase64fromSample(imgSrc: string) {\n        var xhr = new XMLHttpRequest();\n        xhr.onload = function() {\n            var reader = new FileReader();\n            reader.onloadend = function() {\n                const logo = reader.result!.toString();\n                selectedLogo.value = logo;\n                selectedLogoRequest.value = { dataBase64: logo.split(\",\")[1], contentType: \"image/svg+xml\", fullName: \"plateauSampleLogo.svg\" };\n            }\n            reader.readAsDataURL(xhr.response);\n        };\n        xhr.open('GET', imgSrc);\n        xhr.responseType = 'blob';\n        xhr.send();\n    }\n    function removeLogo() {\n        (document.getElementById(\"appLogo\")! as HTMLInputElement).value = \"\";\n        isLogoUploaded.value = false;\n        isSampleLogoSelected.value = false;\n        selectedLogo.value = \"\";\n        doesHaveLogo.value = false;\n    }\n    async function save() {\n        await executor.execute(async () => {\n            emit(\"close\");\n            await applicationUseCase.updateApplication({ ID: props.app!.ID, logo: selectedLogoRequest.value, updateReason: \"userUpdate\" });\n        }, { loading: false })\n        \n    }\n</script>\n\n<style scoped>\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .upload-logo-div {\n        padding-bottom: 32px;\n        border-bottom: 1px solid #D5D5D5;\n        margin: 0 -20px;\n        margin-bottom: 32px;\n        display: flex;\n        justify-content: center;\n        position: relative;\n\n    }\n    .hide-default-input {\n        width: 0.1px;\n        height: 0.1px;\n        opacity: 0;\n        overflow: hidden;\n        position: absolute;\n        z-index: -1;\n    }\n    .upload-logo{\n        /* width: 160px;\n        height: 160px; */\n        /* border: 1px solid #E3E3E3;\n        border-radius: 16px; */\n        font-size: 15px;\n        cursor: pointer;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n    }\n    .ide-logo {\n        width: 160px;\n        height: 160px;\n        opacity: 0.3;\n        border: 1px solid #E3E3E3;\n        border-radius: 16px;\n        padding: 10px;\n\n    }\n    .app-logo-preview,\n    .sample-logo-preview {\n        width: 160px;\n        height: 160px;\n        border: 1px solid #BCBCBC;\n        border-radius: 16px;\n        object-fit: contain;\n\n    }\n    .remove-logo {\n        border-radius: 50%;\n        background: #EEEEEE;\n        color: #757575;\n        font-size: 20px;\n        position: absolute;\n        right: 165px;\n        top: -5px;\n        padding: 3px 10px;\n        cursor: pointer;\n        z-index: 2;\n    }\n    .hover-item {\n        position: absolute;\n        background: #E3E3E3 0% 0% no-repeat padding-box;\n        border-radius: 16px;\n        opacity: 0;\n        width: 160px;\n        height: 160px;\n        cursor: pointer;\n        visibility: hidden;\n        z-index: 1;\n    }\n\n    .upload-logo:hover .hover-item {\n        visibility: visible;\n        opacity: 0.4;\n        transition: visibility 0s, opacity 0.1s ease-in-out;\n    }\n\n    .hover-item-text {\n        position: absolute;\n        z-index: 2;\n        font: normal normal normal 18px/24px Roboto;\n        color: #212121;\n        visibility: hidden;\n        font-size: 14px;\n        font-weight: 500;\n    }\n    .upload-logo:hover .hover-item-text {\n        visibility: visible;\n    }\n    .sample-logo-preview {\n        padding: 20px;\n    }\n    .sample-logo:not(:nth-child(4n)) {\n        margin-right: 9px;\n    }\n    .sample-logo {\n        padding: 22px;\n    }\n    .sample-logo:hover {\n        background: #F5F5F5;\n        border-radius: 8px;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .save-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .save-button:disabled {\n        color: #757575;\n    }\n</style>"],"sourceRoot":""}]);
